<h1 id="tutorials" tabindex="-1"><a class="header-anchor" href="#tutorials" aria-hidden="true">#</a> Tutorials</h1>
<p>Here is the source code from the tutorials available. To run them on your own computer:</p>
<ol>
<li>Unzip the ZIP file.</li>
<li>Go to the root folder of the application you unzipped and want to start.</li>
<li>Install all dependencies (npm packages) by executing the command <code>npm install</code>.</li>
<li>Start the application using <code>npm run serve</code> for Vue.js applications and <code>node app.js</code> (or similar) for Express applications.</li>
</ol>
